.homepage-section {
    background-color: #001427;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .homepage-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start; 
    max-width: 1200px;
    margin: 0 auto;
    padding: 2rem;
  }  
  
  .homepage-content {
    text-align: center;
    max-width: 600px;
    position: relative;
    z-index: 2;
  }

  .homepage-title {
    font-size: 3rem;
    font-weight: 700;
    color: #DBDFAC;
    margin-bottom: 2rem;
  }
  
  .homepage-description {
    font-size: 1.5rem;
    color: #DBDFAC;
    margin-bottom: 2.5rem;
  }
  
  .homepage-button {
    background-color: #00A6FB;
    color: #000e1c;
    font-size: 1.2rem;
    padding: 1rem 2.5rem;
    border-radius: 0.5rem;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .homepage-button:hover {
    background-color: #0054A2;
    scale: 105%;
  }
  
  .homepage-image {
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 400px;
    border-radius: 0.25rem;
  }
